.header{
    margin:0px;
    display: flex;
    justify-content: space-around;
    background-color: #000000;
    text-align: center;
    border-bottom: 1px solid #ffffff;
   
}



.header ul{
display: flex;
}
ul li{ 
    font-size:24px;
    margin-top:12px;
    padding:0px 26px 0px 5px;
    list-style-type: none;
}
.logo{
    border-radius:10px 0px 0 10px;
    padding: 11px 130px 10px 6px;
    background: linear-gradient(90deg, rgba(245,185,66,1) 0%, rgba(0,0,0,1) 60%);
;
}

.navBar{
    display: flex;
    text-align: center;
}

#sort{
    border: none;
    color: #9A9292;
     font-size: 24px; 
}
#sort::placeholder {
    color: #9A9292;
}
.line{
    display: none;
    margin-top:50px;
    padding: 1px 0px 0px 0px;
    position: absolute;
    width: 100vw;
    background-color: #123455;
}